input[type="radio"].saylua-radio {
  position: relative;
  width: 25px;
  height: 25px;
  visibility: hidden; }
  input[type="radio"].saylua-radio::before {
    content: '';
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    visibility: visible;
    width: 100%;
    height: 100%;
    cursor: pointer;
    background-color: #f2f2f2;
    border-radius: 50%;
    border: 2px solid #b3b3b3;
    -webkit-transition: 0.5s background ease-in-out, 0.5s color ease-in-out;
    -o-transition: 0.5s background ease-in-out, 0.5s color ease-in-out;
    transition: 0.5s background ease-in-out, 0.5s color ease-in-out;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center; }
  input[type="radio"].saylua-radio:checked::before, input[type="radio"].saylua-radio:checked:hover::before {
    border-color: var(--mainColor);
    background: radial-gradient(rgba(255, 255, 255, 0) 30%, var(--mainSofterColor)), -webkit-gradient(linear, left top, right bottom, from(var(--mainSofterColor)), color-stop(var(--mainColor)), to(var(--neutralDarkColor)));
    background: -webkit-radial-gradient(rgba(255, 255, 255, 0) 30%, var(--mainSofterColor)), -webkit-linear-gradient(top left, var(--mainSofterColor), var(--mainColor), var(--neutralDarkColor));
    background: -o-radial-gradient(rgba(255, 255, 255, 0) 30%, var(--mainSofterColor)), -o-linear-gradient(top left, var(--mainSofterColor), var(--mainColor), var(--neutralDarkColor));
    background: radial-gradient(rgba(255, 255, 255, 0) 30%, var(--mainSofterColor)), linear-gradient(to bottom right, var(--mainSofterColor), var(--mainColor), var(--neutralDarkColor)); }
  input[type="radio"].saylua-radio:hover::before {
    background: #e6e6e6; }



